export default{
  data () {
    return {
      tabs: ['收支明细', '兑换记录'],
      tabIndex: 0,
      detail: {
        integral_total: 0,
        today_get_integral_limit_total: 100,
        today_get_integral_total: 0
      },
      left: '',
      menuList: {
        total: 1,
        per_page: 15,
        current_page: 1,
        last_page: 1,
        data: []
      },                    // 积分明细
      convertList: {
        total: 1,
        per_page: 15,
        current_page: 1,
        last_page: 1,
        data: []
      }                    // 积分兑换记录
    }
  },
  watch: {
    tabIndex(val) {
      if(val === 1) {
        // 积分明细
        this.getConvertList()
        return
      }
      this.getParticulars()
    }
  },
  onLoad () {
    this.getDetail()
    // 积分明细
    this.getParticulars()
  },
  methods: {
    getDetail() {
      let that= this,
        params= {
          url: that.$api.userIntegralDetails
        }
      that.$httpRequest(params).then(res => {
        that.detail= res.data
        that.left=  res.data.today_get_integral_total / res.data.today_get_integral_limit_total * 100 + '%'
      })
    },
    
    // 签到
    signIn() {
      let that= this
      let params= {
        url: that.$api.signIn
      }
      that.$httpRequest(params).then(res => {
        console.log(res)
      })
    },
    
    // 获取积分明细列表
    getParticulars() {
      let that= this,
        params= {
          url: that.$api.integralLogs,
          data: {
            type: 'all'
          }
        }
      that.$httpRequest(params).then(res => {
        console.log('积分明细', res)
        that.menuList= res.data.list
      })
    },
    
    // 获取积分兑换记录列表
    getConvertList() {
      let that= this,
        params= {
          url: that.$api.lists,
          data: {
            dataType: 'all'
          }
        }
      that.$httpRequest(params).then(res => {
        console.log('兑换明细', res)
        let list= res.data.list
        list.data.map((item, index) => {
          item.create_time= item.create_time.slice(0, 10)
        })
        that.convertList= list
      })
    },
    
    goRules() {
      uni.navigateTo({
        url: './rules'
      })
    },
    goDetail() {
      uni.navigateTo({
        url: '../mine/todayExperience?type=' + 'integral'
      })
    },
    selectTab(index) {
      this.tabIndex = index
    }
  }
}
